/* Form Buttons */

.btn-form-func {
    background: #53c4da;
    border-radius: 4px;
    border: 2px solid #53c4da;
    color: #fff;
    display: inline-block;
    overflow: hidden;
    padding: 20px;
    position: relative;
    text-decoration: none;
    line-height: 1;
    width: 100%;
    font-weight: 600;
    margin-bottom: 10px;
}

.btn-form-func:hover {
    color: #fff;
}

.btn-form-func .btn-form-func-content {
    font-size: 1em;
    line-height: 1.2;
    padding: 0 15px;
    position: relative;
    right: 0;
    transition: right 300ms ease;
    display: block;
    text-align: left;
}

.btn-form-func .icon {
    border-left: 1px solid #fff;
    position: absolute;
    right: 0;
    text-align: center;
    top: 50%;
    transition: all 300ms ease;
    transform: translateY(-50%);
    width: 58px;
    height: 70%;
}

.btn-form-func .icon i {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

.btn-form-func:after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: transparent;
    opacity: 0;
    transition: opacity 300ms ease;
}

.btn-form-func:hover .btn-form-func-content {
    right: 100%;
}

.btn-form-func:hover .icon {
    border-left: 0;
    font-size: 1.4em;
    width: 100%;
}

.btn-form-func:hover:after {
    opacity: .2;
}

.btn-form-func.btn-form-func-alt-color {
    background: #fff;
    border-color: #53c4da;
    color: #53c4da !important;
}

.btn-form-func.btn-form-func-alt-color .icon {
    border-left-color: #53c4da;
}

.btn-form-func.btn-form-func-alt-color:after {
    background-color: transparent;
}

/* Checkboxes */

label.cbx.terms {
    margin: 0;
    padding: 0;
    border: 0;
    height: 22px;
}

label.cbx {
    font-size: 14px;
    font-weight: 500;
    margin-top: 20px;
    border: solid 1px #ddd;
    height: 45px;
    padding: 10px 15px;
    width: 100%
}

.cbx {
    -webkit-user-select: none;
    user-select: none;
    cursor: pointer;
}

.cbx span {
    display: inline-block;
    vertical-align: middle;
    font-size: 14px;
    font-weight: 500;
}

.cbx span:first-child {
    position: relative;
    width: 18px;
    height: 18px;
    transform: scale(1);
    vertical-align: middle;
    border: 1px solid #53c4da;
    transition: all 0.2s ease;
    margin-right: 10px;
}

.cbx span:first-child svg {
    position: absolute;
    top: 3px;
    left: 2px;
    fill: none;
    stroke: #fff;
    stroke-width: 2;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-dasharray: 16px;
    stroke-dashoffset: 16px;
    transition: all 0.3s ease;
    transition-delay: 0.1s;
    transform: translate3d(0, 0, 0);
}

.cbx span:first-child:before {
    content: "";
    width: 100%;
    height: 100%;
    background: #53c4da;
    display: block;
    transform: scale(0);
    opacity: 1;
    border-radius: 50%;
}

.cbx:hover span:first-child {
    border-color: #53c4da;
}

.inp-cbx {
    display: none;
}

.inp-cbx:checked+.cbx span:first-child {
    background: #53c4da;
    border-color: #53c4da;
    animation: wave 0.4s ease;
}

.inp-cbx:checked+.cbx span:first-child svg {
    stroke-dashoffset: 0;
}

.inp-cbx:checked+.cbx span:first-child:before {
    transform: scale(3.5);
    opacity: 0;
    transition: all 0.6s ease;
}

@keyframes wave {
    50% {
        transform: scale(0.9);
    }
}

