    height: 40px;
    background-color: black;
    background-color: rgba(0, 0, 0, 0.6);
    text-align: center;
    padding: 10px;
    line-height: 20px;
    position: fixed;
    bottom: 15px;
    right: 15px;
    cursor: pointer;
    display: none;
    color: #fff;
    font-size: 20px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    border-radius: 3px;
    z-index: 2;
}

/* Success Submit
==================================== */

#success {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 300px;
    height: 190px;
    margin-top: -85px;
    margin-left: -150px;
    text-align: center;
}

#success h4 {
    margin-top: 10px;
    font-family: 'Poppins', sans-serif;
    font-size: 18px;
}

@-webkit-keyframes checkmark {
    0% {
        stroke-dashoffset: 50
    }

    100% {
        stroke-dashoffset: 0
    }
}

@-ms-keyframes checkmark {
    0% {
        stroke-dashoffset: 50
    }

    100% {
        stroke-dashoffset: 0
    }
}

@keyframes checkmark {
    0% {
        stroke-dashoffset: 50
    }

    100% {
        stroke-dashoffset: 0
    }
}

@-webkit-keyframes checkmark-circle {
    0% {
        stroke-dashoffset: 240
    }

    100% {
        stroke-dashoffset: 480
    }
}

@-ms-keyframes checkmark-circle {
    0% {
        stroke-dashoffset: 240
    }

    100% {
        stroke-dashoffset: 480
    }
}

@keyframes checkmark-circle {
    0% {
        stroke-dashoffset: 240
    }

    100% {
        stroke-dashoffset: 480
    }
}

.inlinesvg .svg svg {
    display: inline
}

.icon-order-success svg path {
    -webkit-animation: checkmark 0.25s ease-in-out 0.7s backwards;
    animation: checkmark 0.25s ease-in-out 0.7s backwards
}

.icon-order-success svg circle {
    -webkit-animation: checkmark-circle 0.6s ease-in-out backwards;
    animation: checkmark-circle 0.6s ease-in-out backwards
}

/* Responsive
==================================== */

@media (max-width: 767px) {

    /* Footer
	==================================== */
    footer #copy {
        text-align: left;
        margin-bottom: 25px;
    }

    footer ul li {
        margin-bottom: 10px;
    }
}

@media (max-width: 991px) {

    /* Header
	==================================== */
    header.main-header.sticky {
        position: static;
    }

    header .container {
        width: 100% !important;
        margin-right: auto !important;
        margin-left: auto !important;
        padding-right: 15px !important;
        padding-left: 15px !important;
        width: 100% !important;
        max-width: 100%;
    }

    /* Header Menu Chevron Down */
    .main-menu a i {
        display: none;
    }

    /* Header Icons */
    ul#menuIcons {
        right: 0;
        top: 2px;
    }

    /* Logo */
    #logo {
        position: relative;
        left: 45px;
    }

    /* Sub Header
	==================================== */
    .sub-header {
        margin-top: 0;
    }

    /* Containers
	==================================== */
    .contact {
        padding: 15px 0 0 0;
    }

    /* Contact Info
	==================================== */
    .contact-box {
        margin-top: 15px;
    }

    /* Color Panel */
    #colorPanel {
        display: none;
    }
}
