* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
    box-shadow: none;
    scroll-behavior: auto !important;
}

body {
    background: #fff8eb;
    color: #6f7d95;
    font-family: "Circular Std", sans-serif;
    font-size: 16px;
    line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: #183b56;
    font-weight: 700;
    font-family: "Circular Std", serif;
}

a>h1,
a>h2,
a>h3,
a>h4,
a>h5,
a>h6 {
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

h1 {
    font-size: 70px;
}

h2 {
    font-size: 35px;
}

h3 {
    font-size: 30px;
}

h4 {
    font-size: 24px;
}

h5 {
    font-size: 20px;
    margin-top: -5px;
}

h6 {
    font-size: 16px;
}

p {
    margin-bottom: 15px;
    font-weight: 300;
    font-size: 16px;
    color: #6f7d95;
    line-height: 24px;
}

a {
    color: #6f7d95;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    transition: all 0.3s ease;
    display: inline-block;
    text-decoration: none;
}

a:hover,
a:focus,
a:active {
    color: #f64482;
    text-decoration: none;
    outline: none;
}

img {
    height: auto;
    max-width: 100%;
}

@media (min-width: 576px) {
    h1 {
        font-size: 70px;
    }

    h2 {
        font-size: 48px;
    }

    h3 {
        font-size: 35px;
    }

    h4 {
        font-size: 30px;
    }

    h5 {
        font-size: 24px;
    }

    h6 {
        font-size: 20px;
    }
}

ul {
    margin: 0;
    padding-left: 0;
}

ul li {
    list-style: none;
}

@media (min-width: 1200px) {
    .container {
        max-width: 1190px;
    }
}

/* @@@@@@@@@@@@@  Common CSS Writing Now  @@@@@@@@@@@@@@ */

.padding-tb {
    padding-top: 80px;
    padding-bottom: 55px;
}

@media (min-width: 992px) {
    .padding-tb {
        padding-top: 120px;
